import styles from './index.module.css'
import { exportDatabase, importDatabase, ExportProgress, ImportProgress } from '@/utils/db/data-export'
import * as Progress from '@radix-ui/react-progress'
import { useCallback, useState } from 'react'

export default function DataSetting() {
  const [isExporting, setIsExporting] = useState(false)
  const [exportProgress, setExportProgress] = useState(0)

  const [isImporting, setIsImporting] = useState(false)
  const [importProgress, setImportProgress] = useState(0)

  const exportProgressCallback = useCallback(({ totalRows, completedRows, done }: ExportProgress) => {
    if (done) {
      setIsExporting(false)
      setExportProgress(100)
      return true
    }
    if (totalRows) {
      setExportProgress(Math.floor((completedRows / totalRows) * 100))
    }

    return true
  }, [])

  const onClickExport = useCallback(() => {
    setExportProgress(0)
    setIsExporting(true)
    exportDatabase(exportProgressCallback)
  }, [exportProgressCallback])

  const importProgressCallback = useCallback(({ totalRows, completedRows, done }: ImportProgress) => {
    if (done) {
      setIsImporting(false)
      setImportProgress(100)
      return true
    }
    if (totalRows) {
      setImportProgress(Math.floor((completedRows / totalRows) * 100))
    }

    return true
  }, [])

  const onStartImport = useCallback(() => {
    setImportProgress(0)
    setIsImporting(true)
  }, [])

  const onClickImport = useCallback(() => {
    importDatabase(onStartImport, importProgressCallback)
  }, [importProgressCallback, onStartImport])

  return (
    <div className={styles.tabContent}>
      <div className={styles.section}>
        <span className={styles.sectionLabel}></span>
        <span className={styles.sectionDescription}>
          <strong></strong> Qwerty Learner
           
        </span>
        <span className="pl-4 text-sm font-bold leading-tight text-red-500 text-left"></span>
        <div className="h-3 w-full px-5 flex items-center justify-start">
          <Progress.Root
            className="relative bg-gray-200 rounded-full w-11/12 h-2  overflow-hidden transform translate-z-0"
            value={exportProgress}
          >
            <Progress.Indicator
              className="bg-indigo-400 h-full w-full transition-transform duration-500 ease-out cubic-bezier(0.65, 0, 0.35, 1)"
              style={{ transform: `translateX(-${100 - exportProgress}%)` }}
            />
          </Progress.Root>
          <span className="ml-4 w-10 text-xs font-normal text-gray-600">{`${exportProgress}%`}</span>
        </div>

        <button className="btn-primary ml-4 disabled:bg-gray-300" onClick={onClickExport} disabled={isExporting}>
          
        </button>
      </div>
      <div className={styles.section}>
        <span className={styles.sectionLabel}></span>
        <span className={styles.sectionDescription}>
          <strong className="text-red-500 text-sm font-bold">  </strong>
        </span>

        <div className="h-3 w-full px-5 flex items-center justify-start">
          <Progress.Root
            className="relative bg-gray-200 rounded-full w-11/12 h-2  overflow-hidden transform translate-z-0"
            value={importProgress}
          >
            <Progress.Indicator
              className="bg-indigo-400 h-full w-full transition-transform duration-500 ease-out cubic-bezier(0.65, 0, 0.35, 1)"
              style={{ transform: `translateX(-${100 - importProgress}%)` }}
            />
          </Progress.Root>
          <span className="ml-4 w-10 text-xs font-normal text-gray-600">{`${importProgress}%`}</span>
        </div>

        <button className="btn-primary ml-4 disabled:bg-gray-300" onClick={onClickImport} disabled={isImporting}>
          
        </button>
      </div>
    </div>
  )
}
